body {
  text-align: center;
  background-color: #28456E;
}

.App {
  color: rgb(228, 228, 228);
}

input {
  background-color: #214D86;
  color: rgb(245, 218, 200);
  border-color: rgb(245, 218, 200);
  text-align: center;
  height: 40px;
  width: 200px;
  font-size: 20px;
  font-weight: 800;
  margin-left: 50px;
  margin-top: 50px;
  border-top-style: hidden;
  border-right-style: hidden;
  border-left-style: hidden;
  border-bottom-style: groove;
  outline: none;
  border-radius: 50px;
}

button {
  margin-left: 5px;
  height: 45px;
  width: 45px;
  background-color: #717171;
  color: rgb(230, 230, 230);;
  position: relative;
  border-radius: 50%;
  border: 0px;
  font-size: 20px;
  font-weight: 900;
  outline: none;
}

img {
  height: 80px;
}

footer {
  position: fixed;
  left: 50%;
  bottom: 20px;
  transform: translate(-50%, -50%);
  margin: 0 auto;
}

